* {
  box-sizing: border-box;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #4b4848;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(211, 69, 69);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(184, 40, 40);
}
