.about-content {
  width: 70%;
  margin: auto;
  text-align: center;
  align-items: center;
}

.about-title {
  font-size: 1.5rem;
  opacity: 1;
  animation: TitleFade 3s;
}

@keyframes TitleFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.about-title > span {
  color: aqua;
}

.about {
  font-size: 1.2rem;
  margin-bottom: 1.8em;
  animation: AboutFade 6s;
}

@keyframes AboutFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.aws-btn {
  --button-default-height: 52px;
  --button-default-font-size: 16px;
  --button-default-border-radius: 6px;
  --button-horizontal-padding: 20px;
  --button-raise-level: 5px;
  --button-hover-pressure: 4;
  --transform-speed: 0.185s;
  --button-primary-color: #25292e;
  --button-primary-color-dark: #ffee22;
  --button-primary-color-light: #01e752;
  --button-primary-color-hover: #424e6a;
  --button-primary-border: 0px solid #ffffff;
  --button-secondary-color: #fffc6c;
  --button-secondary-color-dark: #b9b500;
  --button-secondary-color-light: #6c6a00;
  --button-secondary-color-hover: #fffb3e;
  --button-secondary-border: none;
  --button-anchor-color: #f3c8ad;
  --button-anchor-color-dark: #734922;
  --button-anchor-color-light: #4c3016;
  --button-anchor-color-hover: #f1bfa0;
  --button-anchor-border: 1px solid #8c633c;
  margin: 10px 20px;
  animation: BtnFade 4s;
}

@keyframes BtnFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.mobile {
  padding-top: 5%;
  margin-bottom: -5%;
  font-size: 15px;
}

@media (max-width: 991px) and (min-width: 660px) {
  .about-title {
    font-size: 1.3rem;
  }

  .about {
    font-size: 1.1rem;
  }
  .aws-btn {
    --button-default-height: 45px;
    --button-default-font-size: 14px;
  }
}

@media (max-width: 660px) {
  .about-title {
    font-size: 1.2rem;
  }

  .about {
    font-size: 1rem;
  }
  .aws-btn {
    --button-default-height: 40px;
    --button-default-font-size: 12px;
  }
}

@media (max-width: 320px) {
  .about-title {
    font-size: 1.1rem;
  }

  .about {
    font-size: 0.9rem;
  }
  .aws-btn {
    --button-default-height: 35px;
    --button-default-font-size: 10px;
  }
}
