.container {
  display: flex;
  flex-wrap: wrap;
  width: 400px;
  margin-bottom: 20%;
  background-color: whitesmoke;
  border-radius: 25px;
  padding-left: 15px;
}

.page-title {
  margin-left: 22%;
}

.row {
  display: inline;
  flex-direction: column;
  margin: 0 20px;
}

.row img {
  padding: 10px;
}

img {
  width: 80px;
  height: 80px;
  margin: 10px 0;
}

@media (max-width: 660px) {
  .container {
    border-radius: 40px;
  }
  .page-title {
    margin-left: 40%;
  }
}

@media (max-width: 320px) {
  .container {
    border-radius: 80px;
  }
  .page-title {
    margin-left: 40%;
  }
}
