.cardContainer {
  display: flex;
  flex-wrap: wrap;
  height: 90vh;
  justify-content: center;
  align-items: center;
}

.page-title {
  padding: 80px 0 30px 15%;
  margin: 0 auto;
}

@media (max-width: 660px) {
  .page-title {
    padding: 80px 0 30px 0;
    margin: 0 auto;
  }
}
